import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import { SearchOutlined, UnorderedListOutlined } from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import { Helmet } from "react-helmet";
import Flex from "../../util/Flex";

class Notice extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      showTransactions: false,
      currentItem: {},
    };
  }
  m = (key, prefix = "notice.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  showTransactions = (currentItem) => {
    this.setState({ currentItem, showTransactions: true });
  };

  closeTransactions = () => {
    this.setState({ showTransactions: false });
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          width: "5%",
          title: this.m("created"),
          dataIndex: "created",
          sortDirections: "descend",
          defaultSortOrder: true,
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },

        {
          title: this.m("text"),
          dataIndex: "text",
          render: (value, item) => <>{value}</>,
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>{this.m("notice")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("notice")}</h2>

        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/notice/list"}
        />
      </div>
    );
  }
}

export default injectIntl(Notice);
